<template>
	<view class="container">
		<view class="">
			<SearchInput />
			<view class="hot-list">
				<view class="tit">热门搜索</view>
				<view class="hot-box">
					<navigator
						class="item"
						v-for="(item, i) in listHot"
						:key="item.keyboard"
						:url="
							`/pages/search/list/list?keyboard=${item.keyboard}&tbname=${item.tbname}`
						"
					>
						<text class="num">{{ i + 1 }}</text>
						{{ item.keyboard }}
					</navigator>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import SearchInput from '@/pages/search/components/SearchInput.vue'
import { getHotSearch } from '@/api/other.js'
export default {
	name: 'search',
	components: {
		SearchInput,
	},
	data() {
		return {
			listHot: [],
		}
	},
	created() {
		getHotSearch().then(({ data }) => {
			this.listHot = data
		})
	},
}
</script>

<style lang="scss">
/deep/ page,
.swan-web-view {
	background-color: #fff !important;
}

.container {
	background-color: #fff;
	width: 100%;
	height: 100vh;
	.hot-list {
		padding: 8px 20px;
		margin-top: 20px;

		.tit {
			font-size: 14px;
			color: #1a1a1a;
			margin-bottom: 10px;
		}

		.hot-box {
			font-size: 16px;
			color: #181818;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.num {
				display: inline-block;
				width: 20px;
				font-size: 15px;
				font-weight: 700;
				margin-right: 4px;
				color: #bfbfbf;
			}

			.item:nth-child(-n + 3) .num {
				color: #ff942d;
			}

			.item {
				@include textEllipsis(1);
				flex: 48%;
				margin-bottom: 15px;
				box-sizing: border-box;
			}
		}
	}
}
</style>
